import axios from 'axios';

const url = 'api/posts/';

class PostService {
	// Get Posts
	static getPosts() {
    return new Promise ((resolve,reject) => {
      axios.get(url).then((res) => {
        const data = res.data;
        resolve(
          data.map(post => ({
            ...post,
            createdAt: new Date(post.createdAt)
          }))
        );
      })
      .catch((err)=> {
        reject(err);
      })
    });
	}

	// static insertPost(disappearTime, disappearPlace, race, color, age, name, info) {
  //   return axios.post(url, {
  //     disappearTime,
  //     disappearPlace,
  //     race,
  //     color,
  //     age,
  //     name,
  //     info
  //     // formData,
  //   });
	// }

  static insertPost(formData) {
    return axios({
      method: 'post',
      url: url,
      data: formData,
      headers: { 'Content-Type': 'multipart/form-data'}
    });
  }
  

	// Delete Post
	static deletePost(id) {
		return axios.delete(`${url}${id}`);
	}
}

export default PostService;