<template>
  <div class="text-center ma-3">
    <v-dialog v-model="dialog" width="700">
      <template v-slot:activator="{on, attrs}">
        <v-btn color="grey lighten-5" v-bind="attrs" v-on="on">
          Přidat pejska
        </v-btn>
      </template>

      <v-stepper v-model="e1">
        <v-stepper-header class="px-16">
          <v-stepper-step :complete="e1 > 1" step="1" class="px-16" color="grey">
          </v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step step="2" class="px-16" color="grey">
          </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>

          <v-stepper-content step="1">
            <v-form action='/upload' method="POST" enctype="multipart/form-data" class="grey lighten-2">
              <v-container>
                <v-row class="mx-3 pa-6">
                  <v-col cols="12" class="py-0 px-5">
                    <v-text-field solo v-model="posts.name" label="Jméno" required></v-text-field>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-text-field solo v-model="posts.race" label="Rasa" required></v-text-field>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field solo v-model="posts.color" label="Barva" required></v-text-field>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field solo v-model="posts.age" label="Věk" required></v-text-field>
                  </v-col>
                  <v-col>
                    <input type="file" ref="file" name="file" @change="selectFile" >
                  </v-col>
                </v-row>
              </v-container>
              <v-btn color="grey-lighten-4" @click="e1 = 2">Dále</v-btn>
            </v-form>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form class="grey lighten-2">
              <v-container>
                <v-row class="mx-3 pa-6">
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field solo v-model="posts.disappearTime" label="Čas zmizení" prepend-icon="mdi-calendar-range" required></v-text-field>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field solo v-model="posts.disappearPlace" label="Místo zmizení" prepend-icon="mdi-map-marker" required></v-text-field>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-textarea solo v-model="posts.info" label="Dodatečné info" required></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
              <v-btn color="grey-lighten-4" @click="e1 = 1">Zpět</v-btn>
              <v-btn text @click="createPost(); dialog = false">Přidat</v-btn>
            </v-form>
          </v-stepper-content>

        </v-stepper-items>
      </v-stepper>
    </v-dialog>
  </div>
</template>

<script>
import PostService from '../PostService';

export default {
  data() {
    return {
      posts: {
        disappearTime: '',
        disappearPlace: '',
        name: '',
        race: '',
        color: '',
        age: '',
        info: '',
      },
      dialog: false,
      e1: 1,
      file: ''
    }
  },
  methods: {
    async createPost() {
      const formData = new FormData();

      formData.append('disappearTime', this.posts.disappearTime);
      formData.append('disappearPlace', this.posts.disappearPlace);
      formData.append('race', this.posts.race);
      formData.append('color', this.posts.color);
      formData.append('age', this.posts.age);
      formData.append('name', this.posts.name);
      formData.append('info', this.posts.info);
      formData.append('file', this.file);

      await PostService.insertPost(
        formData
      );
      this.posts = await PostService.getPosts();
    },
    selectFile() {
      this.file = this.$refs.file.files[0];
    }
  }
}
</script>